	module.exports = {"data":{"wordpressPost":{"title":"The perception that eating disorders are a women’s problem delays men getting help","content":"<p><a href=\"http://bmjopen.bmj.com/content/4/4/e004342.full\">The widespread perception that only women have eating disorders is preventing men with these problems from getting the help and support they need</a>, indicates a small study published in the online journal <a href=\"http://bmjopen.bmj.com/\">BMJ Open</a>.</p>\n<p><span style=\"line-height: 1.5em\">Estimates suggest that around 1 in 250 women and 1 in 2000 men in the UK have anorexia nervosa, one of the four recognised types of eating disorder &#8211; the others being bulimia nervosa; binge eating disorder; and eating disorder not otherwise specified (EDNOS).</span></p>\n<p>The incidence of eating disorders is on the rise among men, with some estimates suggesting that men now account for one in four cases. But poor recognition of the signs and symptoms of eating disorders in men is likely to mean that the true prevalence may be higher, still, say the authors.</p>\n<p>They interviewed 39 young people between the ages of 16 and 25, 10 of whom were men, about their experiences of eating disorders, in a bid to gauge the impact of gender on diagnosis, treatment, and support.</p>\n<p>The interviews were carried out to inform an online patient resource (Healthtalkonline), and participants were recruited from patient organisations, social media, and healthcare professionals.</p>\n<p>Four themes emerged from the interviews: recognition of early signs and symptoms; recognition of the problem; getting help; and initial contact with healthcare and support services.</p>\n<p>All the men took some time to realise that their experiences and behaviours were potential signs and symptoms of an eating disorder during which time these became entrenched.</p>\n<p>Their behaviours included going days without eating; purging; and obsessive calorie counting, exercise, and weighing. Some also self-harmed and increasingly isolated themselves from others.</p>\n<p>The perception that eating disorders are a women’s problem, and particularly a problem for young women, was cited as one of the main reasons why it took them so long to understand what was happening.</p>\n<p>One young man, who described himself as “one of the lads,” said he thought eating disorders only affected “fragile teenage girls,” while another said he thought these disorders were “something girls got.”</p>\n<p>None of the men was aware of the symptoms of an eating disorder, and friends, family, and teachers were also very slow to recognise the symptoms, frequently putting the changed behaviours down to personal choices.</p>\n<p>It was only reaching a crisis point or being admitted as an emergency that triggered the realisation of what was happening to them, the men said.</p>\n<p>They also delayed seeking help because they feared they wouldn’t be taken seriously by healthcare professionals, or didn’t know where to go for support.</p>\n<p>And their experiences of the healthcare system were mixed. They said they often had to wait a long time for specialist referral and had sometimes been misdiagnosed, or, as in one case, told by the doctor “to man up.” They complained of insufficient information about eating disorders targeted specifically at men.</p>\n<p>“Men with eating disorders are underdiagnosed, undertreated and under researched,” write the authors.</p>\n<p>“Our findings suggest that men may experience particular problems in recognising that they may have an eating disorder as a result of the continuing cultural construction of eating disorders as uniquely or predominantly a female problem,” they add.</p>\n<p>This perception has “also been embedded in clinical practice,” they note, adding that in order to improve the outlook for men with eating disorders, “early detection is imperative.”</p>\n<p>Eating disorders cost the NHS between £50 and £70 million, while anorexia has the highest death rate of all adolescent psychiatric conditions, they point out.<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"b6b5adce-10db-57f6-8dc6-203634828dd6"}}

